export const changeEmailValidate = (newEmail, password) => {
  const validateEmail = (email) => {
    const re = /\S+@\S+\.\S+/;
    return re.test(email);
  };

  const emailForm = validateEmail(newEmail);

  if (!newEmail || !password) {
    return 'Wszystkie pola powinny zosta wypenione';
  } else if (!emailForm) {
    return 'Niepoprawny adres email';
  }
};

export const changePasswordValidation = (
  newPassword,
  repeatedNewPassword,
  oldPassword
) => {
  if (!newPassword || !repeatedNewPassword || !oldPassword) {
    return 'Wszystkie pola powinny zosta wypenione';
  } else if (newPassword.length < 6 || newPassword.length > 40) {
    return 'Haso powinno skada si z minimalnie 6, a maksymalnie 40 znakw';
  } else if (newPassword !== repeatedNewPassword) {
    return 'Wartoci nowego hasa nie s identyczne';
  }
};
